canvas {
    display: flex;
    flex: 1;
    width: 100%;
    height: 100%;
    background-color: antiquewhite;
}
.canvas-container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
 } 
.App {
    font-family: sans-serif;
    text-align: center;
 }
.scroll {
    overflow: hidden;
 }
html,body{
  overflow:hidden;
  position: fixed;
 }
 @font-face {
  font-family: 'myfont';
  src: url('font/SDcom2usGothicNeoRoundTTF-fSb.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
}
input, textarea {
    font-size: 30px; /* 최소 16px로 설정하여 확대 방지 */
}